@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin:0;
}
body {
  background-color: #f5f6fa;
}
.App_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.App_title {
  font-family: 'Poppins', sans-serif;
  letter-spacing: 0.2rem;
}
.App_dropdown_img {
  margin-left: auto;
  width: 7%;
}
.Stats_box {
  display: flex;
  justify-content: space-between;
  padding: 2em;
}
.App {
  display: flex;
  justify-content: space-evenly;
  padding: 2em;
}
.App_left {
  flex:0.9 1;
}
.App_right {
  display: flex;
  flex-direction: column;
}
.App_right .MuiCardContent-root {
  display: flex;
  flex-direction: column;
  flex-grow:1;
}
.App_graph{
  flex-grow:1;
}
@media(max-width:700px) {
  .App {
    flex-direction: column;
  }
}

.InfoBox {
    flex:1 1;
    cursor: pointer;
     box-shadow: 0px 35px 77px -17px rgba(0,0,0,0.33); 
     transition: all 0.4s ease-out; 
}
.InfoBox:hover {
    transform: scale(1.05);
}
.InfoBox:not(:last-child) {
    margin-right: 15px;
}

.InfoBox_cases {
    color:rgb(233, 72, 72);
    font-size: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}
.InfoBox_total {
    color:#6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
}

.InfoBox--selected {
    border-top:10px solid rgb(14, 218, 99);
}

.InfoBox--red {
    border-color:  rgb(228, 106, 106);
}

.InfoBox_cases--green{
    color:rgb(43, 170, 43);
}
.InfoBox_today {
    color:rgb(156, 156, 156)
}
.Map {
    height: 500px;
    background-color: #fff;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
}
.Map .leaflet-container{
    height: 100%;
}
.Popup_flag img {
    width: 100px;
}
.Popup_flag {
    height: 80px;
    width: 100%;
    background-size:cover;
    border-radius:8px;
}
.Popup_container {
    width: 150px;
}
.Popup_name {
    font-size: 1rem;
    font-weight: bold;
    color:#555;
}
.table {
    margin-top: 2em;
    overflow:scroll;
    height:400px;
}
.table tr:nth-of-type(odd) {
    background-color: rgb(240, 227, 227);
}
.table tr {
    display:flex;
    justify-content: space-between;
}
.table td {
    padding: 0.5rem;
}

